footer {
  background: rgba(0, 0, 0, .6);
  padding: 45px 15px;

  .change-page {
    width: 205px;

    .change-page__prev {
      a {
        padding: 12px 24px;
        background: rgba(0, 0, 0, .3);
        color: rgba(255, 255, 255, .3);
        text-decoration: none;

        &:hover {
          color: rgba(0, 0, 0, .3);
          background: rgba(255, 255, 255, .3);
        }
      }
    }
  }
}